@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/Roboto-Light.otf") format("otf"), url("../fonts/Roboto-Light.svg") format("svg"), url("../fonts/Roboto-Light.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Roboto-Regular.otf") format("otf"), url("../fonts/Roboto-Regular.svg") format("svg"), url("../fonts/Roboto-Regular.woff") format("woff");
}
.slidecontainer {
  width: 100%;
  font-weight: 300;
}

.slider {
  -webkit-appearance: none;
  margin: 14px;
  width: 50%;
  height: 8px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.6;
  transition: opacity 0.2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 19px;
  height: 19px;
  border-radius: 50%;
  background: #975e5e;
  cursor: pointer;
}